import React from 'react';
import {CDropdown, CDropdownMenu, CDropdownToggle, CDropdownItem} from '@coreui/react';

export default ()=>{
  /*example start: template*/
  return (
    <div>
    <CDropdown direction="up" className="mr-1" custom={false}>
                <CDropdownToggle caret color="secondary">
                  Direction Up
                </CDropdownToggle>
                <CDropdownMenu>
                  <CDropdownItem header>Header</CDropdownItem>
                  <CDropdownItem disabled>Action Disabled</CDropdownItem>
                  <CDropdownItem>Action</CDropdownItem>
                  <CDropdownItem>Another Action</CDropdownItem>
                </CDropdownMenu>
              </CDropdown>
    </div>
  )
  /*example end: template*/
}
